@import url(https://fonts.googleapis.com/css2?family=Roboto:ital@1&family=Rowdies&display=swap);
body{
  background-image: linear-gradient(to bottom, rgb(180, 225, 255), rgb(45, 171, 255));
  background-repeat: no-repeat;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  margin: 0;
  padding: 0;
  position: relative;
  min-height: 100vh;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body::after {
  content: '';
  display: block;
  height: 35px;
}

html, #root{
  height: 100%;
}

button {
  font-family: inherit
}




